
.container {
  margin: 0 auto;
  margin-top: 5px;
  background-color: white;
  padding: 20px;
  padding-left: 50px;
  box-shadow: 2px 1px 4px 1px rgba(0, 0, 0, 0.4);
  display: flex;
  justify-content: space-between ;
  height: 20%;
 
}
.logo{
  height: 20px;
}
.off{
  margin-top: -20px;
  color: rgba(99, 99, 99, 0.872);
  padding: 15px;
  cursor: pointer;
}
.p{
  padding: 0px;
  margin: 0px;
  margin-top:5px;
  padding-right: 8px;
  color: rgb(243, 46, 7);
  text-transform: uppercase;
  font-size: smaller;
}
.add{
  margin-top: 50%;
  margin-left: 25%;
  color: rgba(243, 46, 7, 0.872);
  background-color: transparent;
  cursor: pointer;
  /* border: 1px solid rgba(228, 61, 0, 0.872); */
  /* box-shadow: 0px 2px 8px 2px rgba(0, 0, 0, 0.258); */
}
.add:hover{
  color:rgba(243, 46, 7, 0.578);
}


.buttonCont{
  align-content: center;
  display: flex;
  flex-direction: column;
}
.h3{
  text-align: center;
  color: rgb(255, 55, 0);
  text-shadow: 2px 1px 4px 1px rgba(0, 0, 0, 0.4);
}
@media only screen and (max-width: 991px) {  
  .addnote{
    font-weight:lighter;
    font-size: 14px;
  }
  
}

